.catalog-form__group {
  margin: 0;
  padding: 0;
  border: 0;
}

.catalog-form__group--bg-color {
  @media (min-width: $tablet-width) {
    background-color: $section-bg-color;
  }
}

.catalog-form__wrapper {
  position: relative;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;

  @media (max-width: 767px) {
    padding: 0 30px;
    margin-bottom: 33px;

    &--name {
      margin-bottom: 34px;
    }

    &--additionally {
      margin-bottom: 6px;
    }

    &--tel {
      margin-bottom: 36px;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 0 50px;
    display: flex;
    padding-top: 50px;
    padding-bottom: 50px;

    &--radio {
      padding-bottom: 50px;
    }

    &--tel {
      padding-bottom: 0;
      margin: 0 auto 24px auto;
      width: 768px;
    }

    &--mail {
      padding-top: 0;
      margin: 0 auto;
      width: 768px;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 50px 223px 50px 111px;

    &--check {
      padding-right: 190px;
    }

    &--radio {
      margin-bottom: -1px;
    }

    &--tel {
      padding-bottom: 0;
      padding-left: 0;
      padding-right: 0;
      margin: 0 auto 24px auto;
      width: 928px;
    }

    &--mail {
      padding-top: 0;
      margin: 0 auto;
      padding-left: 0;
      padding-right: 0;
      width: 928px;
    }

    &--additionally {
      padding-right: 225px;
    }
  }
}

.catalog-form__title {
  color: $tiffany;
  padding: 0;
  width: max-content;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 700;
  padding-right: 20px;

  @media (max-width: 767px) {
    background-color: #ffffff;

    &::after {
      content: "";
      position: absolute;
      background-color: $tiffany;
      left: 90px;
      height: 1px;
      right: 30px;
      top: 6px;
      z-index: -1;
    }

    &--check {
      margin-bottom: 25px;
    }

    &--radio {
      margin-bottom: 34px;
    }

    &--name {
      margin-bottom: 29px;
    }

    &--tel {
      margin-top: 1px;
    }
  }

  @media (min-width: $tablet-width) {
    padding-top: 17px;

    &--radio {
      padding-top: 19px;
      margin-bottom: 29px;
    }

    &--check {
      padding-top: 17px;
    }

    &--name {
      padding-top: 18px;
    }

    &--mail {
      min-width: 88px;
      padding-top: 18px;
    }

    &--additionally {
      padding-top: 20px;
    }
  }

  @media (min-width: $desktop-width) {
    &--radio {
      padding-top: 20px;
    }

    &--check {
      padding-top: 18px;
    }
  }
}

.field-tel {
  position: relative;

  @media (max-width: 767px) {
    height: 55px;

    input {
      height: 55px;
    }
  }

  @media (min-width: $tablet-width) {
    width: 501px;
    margin-left: 115px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    margin-left: 62px;
    margin-right: 114px;
  }

  input:hover,
  input:active {
    border-bottom: 2px solid #62d1ba;
  }

  input:hover + .field-tel__img g,
  input:active + .field-tel__img g,
  input:focus + .field-tel__img g {
    opacity: 1;

    & path {
      fill: #62d1ba;
    }
  }
}

.field-tel__img {
  position: absolute;

  @media (max-width: 767px) {
    top: 14px;
    right: 7px;
  }

  @media (min-width: $tablet-width) {
    top: 11px;
    right: 26px;
  }

  @media (min-width: $desktop-width) {
    right: 8px;
  }
}

.field-mail {
  position: relative;

  @media (max-width: 767px) {
    height: 58px;
    padding-top: 3px;
  }

  @media (min-width: $tablet-width) {
    width: 501px;
    margin-left: 80px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    margin-left: 27px;
    margin-right: 114px;
  }

  input:hover,
  input:active {
    border-bottom: 2px solid #62d1ba;
  }

  input:hover + .field-mail__img path,
  input:active + .field-mail__img path,
  input:focus + .field-mail__img path {
    fill: #62d1ba;
    opacity: 1;
  }
}

.field-mail__img {
  position: absolute;

  @media (max-width: 767px) {
    top: 19px;
    right: 3px;
  }

  @media (min-width: $tablet-width) {
    top: 16px;
    right: 22px;
  }

  @media (min-width: $desktop-width) {
    right: 4px;
  }
}

.catalog-form__list {
  @include reset-list;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-grow: 1;
    margin-left: 105px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 53px;
  }

  .control {
    position: relative;
    cursor: pointer;

    @media (max-width: 767px) {
      height: 38px;
    }

    @media (min-width: $tablet-width) {
      justify-content: flex-start;
    }
  }
}

.catalog-form__list--radio {
  .control {
    padding-left: 60px;
    min-height: 32px;

    @media (min-width: $tablet-width) {
      padding-left: 85px;
    }
  }

  .control__input[type="radio"]:checked + .control__mark::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-color: $tiffany;
    top: 10px;
    left: 10px;
    border-radius: 50%;

    @media (min-width: $tablet-width) {
      width: 20px;
      height: 20px;
      top: 13px;
      left: 13px;
    }
  }

  .control__input[type="radio"] + .control__mark {
    position: absolute;
    content: "";
    width: 40px;
    height: 40px;
    border: 2px solid #444444;
    border-radius: 50%;
    top: -3px;
    left: 0;

    @media (min-width: $tablet-width) {
      width: 50px;
      height: 50px;
      top: 1px;
      left: 8px;
    }

    @media (min-width: $desktop-width) {
      left: 5px;
    }
  }

  .control__input[type="radio"]:hover + .control__mark {
    border: 2px solid #62d1ba;
  }

  .control__input[type="radio"]:active + .control__mark {
    border: 2px solid #b3b3b3;
  }

  .catalog-form__item:not(:last-of-type) {
    margin-bottom: 13px;

    @media (min-width: $tablet-width) {
      margin-bottom: 24px;
    }
  }
}

.catalog-form__list--checkbox {
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    flex-grow: 1;
    display: grid;
    grid-template-columns: 239px 239px;
    gap: 23px;
    row-gap: 24px;
    padding-left: 103px;
    margin: 0;

    & .catalog-form__item {
      margin: 0;
    }
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 239px 239px 239px;
    padding-left: 51px;
  }

  .control__input[type="checkbox"] + .control__mark {
    content: "";
    position: absolute;
    width: 38px;
    height: 38px;
    border: 2px solid #444444;
    top: -1px;
    left: 0;

    @media (min-width: $tablet-width) {
      width: 52px;
      height: 52px;
      top: 0;
      left: 0;
    }

    @media (min-width: $desktop-width) {
      top: -2px;
    }
  }

  .control__input[type="checkbox"]:hover + .control__mark {
    border: 2px solid #62d1ba;
  }


  .control__input[type="checkbox"]:active + .control__mark {
    border: 2px solid #b3b3b3;
  }

  .control__input[type="checkbox"]:checked + .control__mark::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 17px;
    background-image: url("../img/svg/checkbox-tick.svg");
    background-repeat: no-repeat;
    background-size: cover;
    top: 9px;
    left: 5px;

    @media (min-width: $tablet-width) {
      width: 32px;
      height: 22px;
      top: 14px;
      left: 8px;
    }
  }

  .catalog-form__item:not(:last-of-type) {
    @media (max-width: 767px) {
      margin-bottom: 23px;
    }
  }
}

.control {
  padding-left: 61px;

  @media (min-width: $tablet-width) {
    display: block;
    padding: 17px 0;
    padding-left: 81px;
  }
}

.catalog-form__item {
  align-self: start;

  label {
    font-size: 17px;
    line-height: 18px;
    font-weight: 700;

    @media (min-width: $tablet-width) {
      font-size: 19px;
      align-self: center;
    }
  }

  .control-label {
    line-height: 18px;
  }

  input:hover,
  input:active {
    border-bottom: 2px solid #62d1ba;
  }
}

.data {
  @media (min-width: $tablet-width) {
    margin-right: 25px;
  }
}

.personal-data {
  display: flex;
  flex-direction: column;
  align-self: stretch;

  &:not(:last-of-type) {
    margin-bottom: 23px;
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;

    &:not(:last-of-type) {
      margin-bottom: 24px;
    }
  }

  input {
    border: none;
    border-bottom: 2px solid #000000;
  }
}

input,
textarea {
  width: 100%;
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  height: 51px;
  padding: 4px 0 0 0;
  border: none;
  outline: none;
  border-bottom: 2px solid #000000;
  background-color: transparent;

  @media (min-width: $tablet-width) {
    font-size: 19px;
    padding: 17px 17px 16px 17px;
    height: 52px;
  }

  &::placeholder {
    opacity: 0.3;
    font-size: 17px;
    line-height: 18px;
    font-family: "Open Sans", sans-serif;

    @media (min-width: $tablet-width) {
      font-size: 19px;
      padding: 0;
    }
  }
}

.catalog-form__textarea {
  padding: 16px 15px;
  border: 2px solid #444444;
  min-height: 142px;

  @media (max-width: 767px) {
    margin-top: 21px;
  }

  @media (min-width: $tablet-width) {
    margin-left: 108px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 55px;
  }

  &:hover,
  &:active,
  &:focus {
    border: 2px solid #62d1ba;
  }
}

.catalog-form__text {
  font-size: 14px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    order: 2;
    align-self: center;
    font-size: 17px;
  }
}

.catalog-form__inner {
  @media (min-width: $mobile-width) and (max-width: 767px) {
    padding: 0 30px;
    margin-bottom: 55px;
  }

  @media (min-width: $tablet-width) {
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
    margin-top: 4px;
    margin-bottom: 49px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 225px;
    margin-top: 0;
  }
}

.catalog-form__button {
  width: 230px;
  padding: 13px 0 12px 0;
  font-size: 17px;
  cursor: pointer;

  @media (min-width: $mobile-width) and (max-width: 767px) {
    width: 260px;
    margin: 0 auto;
    margin-top: 27px;
    margin-bottom: 16px;
  }

  &:hover {
    background-color: #444444;
    color: #ffffff;
  }

  &:active,
  &:focus-within {
    background-color: #444444;
    color: #7c7c7c;
  }
}
